import React, { useState, useEffect } from 'react';
import Header from '../components/layout/Header';
import Footer from '../components/layout/Footer';
import ScrollToTop from '../components/ui/ScrollToTop';
import HeroWithSkills from '../components/sections/HeroWithSkills';

const HomePage: React.FC = () => {
  const [isThemeInitialized, setIsThemeInitialized] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  useEffect(() => {
    let timerId: NodeJS.Timeout;
    try {
      const savedTheme = localStorage.getItem('theme');
      const systemPrefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
      let initialTheme: 'dark' | 'light';

      if (savedTheme === 'dark' || savedTheme === 'light') {
        initialTheme = savedTheme;
      } else {
        initialTheme = systemPrefersDark ? 'dark' : 'light';
      }

      if (initialTheme === 'dark') {
        document.documentElement.classList.add('dark');
      } else {
        document.documentElement.classList.remove('dark');
      }
    } catch (error) {
      console.error('Error initializing theme:', error);
      // Default to light theme in case of error
      document.documentElement.classList.remove('dark');
    } finally {
      setIsThemeInitialized(true);
      // Simulate loading time
      timerId = setTimeout(() => {
        setIsLoading(false);
      }, 500);
    }

    return () => {
      if (timerId) {
        clearTimeout(timerId);
      }
    };
  }, []);

  if (!isThemeInitialized || isLoading) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-background text-foreground">
        <div className="animate-pulse flex flex-col items-center space-y-4">
          <div className="h-12 w-12 rounded-full bg-primary/20"></div>
          <div className="h-4 w-32 bg-primary/20 rounded"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex flex-col bg-background text-foreground transition-colors duration-300">
      <Header />
      <main className="flex-1 p-4 sm:p-6 mb-6">
        <HeroWithSkills />
      </main>
      <Footer />
      <ScrollToTop />
    </div>
  );
};

export default HomePage;
