import React, { useEffect, useRef, useState, memo } from 'react';
import gsap from 'gsap';

const AnimatedHeroBackground = memo(() => {
  const [prefersReducedMotion, setPrefersReducedMotion] = useState(
    () => window.matchMedia('(prefers-reduced-motion: reduce)').matches
  );
  const containerRef = useRef<SVGSVGElement>(null);
  const animationTimeline = useRef<gsap.core.Timeline | null>(null);
  const linesRef = useRef<SVGLineElement[]>([]);

  useEffect(() => {
    const mediaQuery = window.matchMedia('(prefers-reduced-motion: reduce)');
    const handleChange = () => {
      setPrefersReducedMotion(mediaQuery.matches);
    };

    mediaQuery.addEventListener('change', handleChange);
    return () => mediaQuery.removeEventListener('change', handleChange);
  }, []);

  useEffect(() => {
    const svg = containerRef.current;

    // Cleanup function to kill animations and remove lines
    const cleanup = () => {
      if (animationTimeline.current) {
        animationTimeline.current.kill();
        animationTimeline.current = null;
      }
      if (linesRef.current.length > 0) {
        linesRef.current.forEach(line => line.remove());
        linesRef.current = [];
      }
    };

    if (prefersReducedMotion || !svg) {
      cleanup(); // Ensure cleanup if motion is reduced or SVG not present
      return;
    }
    
    // Clear any existing lines from previous renders (e.g. if reduced motion was toggled)
    cleanup();


    const numLines = 25;
    const brandPrimaryHsl = { h: 181, s: 48, l: 32 };
    const { width, height } = svg.getBoundingClientRect();
    
    if (width === 0 || height === 0) { // Don't animate if SVG has no dimensions
        return;
    }

    animationTimeline.current = gsap.timeline({ repeat: -1 });

    for (let i = 0; i < numLines; i++) {
      const line = document.createElementNS('http://www.w3.org/2000/svg', 'line');
      svg.appendChild(line);
      linesRef.current.push(line);

      const length = gsap.utils.random(50, 250);
      const angle = gsap.utils.random(0, 360);
      const x1 = gsap.utils.random(0, width);
      const y1 = gsap.utils.random(0, height);
      const x2 = x1 + length * Math.cos(angle * Math.PI / 180);
      const y2 = y1 + length * Math.sin(angle * Math.PI / 180);
      const initialOpacity = gsap.utils.random(0.02, 0.1);

      gsap.set(line, {
        attr: { x1, y1, x2, y2, 'stroke-width': gsap.utils.random(0.1, 0.5) },
        stroke: `hsla(${brandPrimaryHsl.h}, ${brandPrimaryHsl.s}%, ${brandPrimaryHsl.l}%, ${initialOpacity})`,
      });

      animationTimeline.current.to(line, {
        opacity: gsap.utils.random(0.05, 0.2),
        duration: gsap.utils.random(2, 5),
        yoyo: true,
        repeat: -1,
        ease: 'sine.inOut',
      }, gsap.utils.random(0, 2)); // Stagger start times

      if (i % 3 === 0) {
        animationTimeline.current.to(line, {
          attr: {
            x1: `+=${gsap.utils.random(-width * 0.1, width * 0.1)}%`,
            y1: `+=${gsap.utils.random(-height * 0.1, height * 0.1)}%`,
            x2: `+=${gsap.utils.random(-width * 0.1, width * 0.1)}%`,
            y2: `+=${gsap.utils.random(-height * 0.1, height * 0.1)}%`,
          },
          duration: gsap.utils.random(5, 10),
          yoyo: true,
          repeat: -1,
          ease: 'power1.inOut',
        }, gsap.utils.random(0, 5)); // Stagger start times for drift
      }
    }

    return cleanup; // Cleanup on unmount or if prefersReducedMotion changes

  }, [prefersReducedMotion]);

  return (
    <div
      className="absolute inset-0 overflow-hidden -z-10 pointer-events-none"
      aria-hidden="true"
    >
      {prefersReducedMotion ? (
        <svg width="100%" height="100%" className="block" preserveAspectRatio="xMidYMid slice">
          <line x1="10%" y1="20%" x2="90%" y2="30%" stroke="hsla(181, 48%, 32%, 0.07)" strokeWidth="0.2" />
          <line x1="20%" y1="90%" x2="70%" y2="10%" stroke="hsla(181, 48%, 32%, 0.05)" strokeWidth="0.2" />
          <line x1="50%" y1="50%" x2="100%" y2="45%" stroke="hsla(181, 48%, 32%, 0.08)" strokeWidth="0.2" />
          <line x1="5%" y1="70%" x2="60%" y2="80%" stroke="hsla(181, 48%, 32%, 0.06)" strokeWidth="0.2" />
          <line x1="30%" y1="5%" x2="40%" y2="95%" stroke="hsla(181, 48%, 32%, 0.04)" strokeWidth="0.2" />
        </svg>
      ) : (
        <svg ref={containerRef} width="100%" height="100%" className="block" preserveAspectRatio="xMidYMid slice" />
      )}
    </div>
  );
});

AnimatedHeroBackground.displayName = 'AnimatedHeroBackground';

export default AnimatedHeroBackground;
