import React, { useEffect, useRef, useState } from 'react';
import { motion } from 'framer-motion';
import gsap from 'gsap';
import { TextPlugin } from 'gsap/TextPlugin';
import { useIsMobile } from '../hooks/use-mobile.ts';
import { Badge } from '../ui/badge.tsx';
import { Button } from '../ui/button.tsx';
import CaseStudyCard from '../CaseStudyCard.tsx';
import AnimatedHeroBackground from '../ui/AnimatedHeroBackground.tsx';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '../ui/accordion.tsx';
import { Download, Mail, Phone, Linkedin, MessageCircle, GraduationCap, ShoppingCart, Network, Rocket } from 'lucide-react';

gsap.registerPlugin(TextPlugin);

const PHRASES = [
  "Simplifying Enterprise Experiences",
  "UX Designer specializing in Enterprise SaaS",
  "Crafting User-Centric Digital Experiences",
  "Transforming Complexity into Clarity",
  "Expert in EdTech & HR Tech"
];

const INDUSTRIES = [
  { title: "EdTech", iconName: "GraduationCap", content: "Led UX strategy and design for SIS, LMS, Recruitment App, and Student Portals. Revamped SIS through evaluation, design systems, and redesign of 30+ features. Directed LMS design focusing on accessibility, usability, and scalable workflows. Designed Student Portal for seamless navigation and mobile responsiveness. Created eLearning platform for trading courses." },
  { title: "eCommerce", iconName: "ShoppingCart", content: "Redesigned key flows for a B2B pharma eCommerce site. Designed eCommerce experiences for brands like Robinson Co. and Natural Partners. Improved product discovery, navigation and checkout flows. Crafted scalable UI systems for quick orders, subscriptions, and account management." },
  { title: "Connected Tech", iconName: "Network", content: "Led design for connected home interfaces (consumer & industrial IoT). Founding member of product/design team, revamped platform, built design system. Designed subscription-based water purifier interface and mobile app for idly dosa batter making machine. Experience includes designing asset management solutions for real-time tool tracking in industrial IoT settings." },
  { title: "Startup", iconName: "Rocket", content: "Contributed to the design and development of various startup products, focusing on user-centered design and rapid iteration." }
];

const ICON_MAP: { [key: string]: React.ElementType } = {
  GraduationCap,
  ShoppingCart,
  Network,
  Rocket
};

const BRANDS = [
  { src: "/Elanable-uploads/Camu.png", alt: "Camu" },
  { src: "/Elanable-uploads/Mastek.png", alt: "Mastek" },
  { src: "/Elanable-uploads/Geninfy.jpg", alt: "Geninfy" },
  { src: "/Elanable-uploads/Oceo.svg", alt: "Oceo" }
];

const STATS = [
  { label: "Years Exp", value: "9+" },
  { label: "Products", value: "10+" },
  { label: "Features", value: "30+" },
  { label: "Design systems", value: "2" }
];

const CONTACT_ITEMS = [
  { type: "email", Icon: Mail, text: "elanthamilan@gmail.com", href: "mailto:elanthamilan@gmail.com" },
  { type: "phone", Icon: Phone, text: "+91 81486 22302", href: "tel:+918148622302" },
  { type: "whatsapp", Icon: MessageCircle, text: "WhatsApp", href: "https://wa.me/918148622302" },
  { type: "linkedin", Icon: Linkedin, text: "LinkedIn", href: "https://www.linkedin.com/in/elanthamilan/" },
];

const caseStudiesData = [
  {
    title: "Campus Hiring Platform",
    year: "2023",
    who: "Top Indian University",
    what: "End-to-end campus hiring solution",
    result: "50% faster hiring",
    image: "/Elanable-uploads/Camu Campus Recruitment App.png",
    link: "/case-study/campus-hiring",
    tags: ['EdTech', 'SaaS', 'UX/UI'],
    description: "A platform to streamline campus hiring processes.",
    index: 0,
    cardClassName: "bg-accent-light dark:bg-slate-800/60",
    accentColorName: "brand-primary",
    buttonVariant: "default" as const,
  },
  {
    title: "AI Student Course Planner",
    year: "2025",
    who: "University Students",
    what: "AI-powered course planning tool",
    result: "60% less scheduling time",
    image: "/Elanable-uploads/78be9a59-8230-4ea5-b024-d96fd3cdd83e.png",
    link: "/case-study/student-planner",
    tags: ["AI", "UX Research", "EdTech"],
    description: "A redesign of a student course planning tool with AI integration.",
    index: 1,
    cardClassName: "bg-accent-bg",
    accentColorName: "brand-secondary",
    buttonVariant: "secondary" as const,
  }
];

const Hero = React.memo(() => {
  const [loaded, setLoaded] = useState(false);
  const isMobile = useIsMobile();
  const headlineRef = useRef<HTMLHeadingElement>(null);
  const animationTimeline = useRef<gsap.core.Timeline | null>(null);
  const reducedMotionMatcher = useRef<MediaQueryList | null>(null);

  useEffect(() => {
    setLoaded(true);

    const headlineElement = headlineRef.current;
    if (!headlineElement) return;

    const prefersReducedMotion = window.matchMedia('(prefers-reduced-motion: reduce)');
    reducedMotionMatcher.current = prefersReducedMotion;

    const handleReducedMotionChange = (event: MediaQueryListEvent) => {
      if (event.matches) {
        if (animationTimeline.current) {
          animationTimeline.current.pause();
          gsap.set(headlineElement, { text: PHRASES[0] });
        }
      } else {
        if (animationTimeline.current) {
          animationTimeline.current.play();
        } else {
          // Re-initialize if it was never created
          if (headlineElement.textContent !== PHRASES[0]) { // Check if it's not already on the first phrase
             gsap.set(headlineElement, { text: PHRASES[0] }); // Reset to first phrase before starting
          }
          animationTimeline.current = gsap.timeline({ repeat: -1, repeatDelay: 2 });
          PHRASES.forEach((phrase, index) => {
            animationTimeline.current!.to(headlineElement, {
              duration: 1.5,
              text: phrase,
              ease: "power1.inOut",
            }, `+=${index === 0 ? 0 : 2}`); // Delay between phrases
          });
        }
      }
    };

    if (prefersReducedMotion.matches) {
        gsap.set(headlineElement, { text: PHRASES[0] });
    } else {
        animationTimeline.current = gsap.timeline({ repeat: -1, repeatDelay: 2 });
        PHRASES.forEach((phrase, index) => {
            animationTimeline.current!.to(headlineElement, {
            duration: 1.5,
            text: phrase,
            ease: "power1.inOut",
            }, `+=${index === 0 ? 0 : 2}`);
        });
    }

    prefersReducedMotion.addEventListener('change', handleReducedMotionChange);

    return () => {
      prefersReducedMotion.removeEventListener('change', handleReducedMotionChange);
      if (animationTimeline.current) {
        animationTimeline.current.kill();
        animationTimeline.current = null;
      }
    };
  }, []);

  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text).then(() => {
      alert("Copied to clipboard!"); // Or use a toast notification
    }).catch(err => {
      console.error("Failed to copy:", err);
    });
  };

  return (
    <section className={`relative py-16 md:py-24 text-foreground bg-background transition-opacity duration-1000 ${loaded ? 'opacity-100' : 'opacity-0'}`}>
      <AnimatedHeroBackground />
      <div className="container mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
        <div className="grid md:grid-cols-2 gap-12 items-start">
          {/* Left Column */}
          <div className="space-y-6 md:space-y-8 text-center md:text-left">
            <Badge variant="secondary" className="text-sm sm:text-base mx-auto md:mx-0 w-fit">Enterprise UX Designer</Badge>
            <h1 ref={headlineRef} className="text-4xl sm:text-5xl lg:text-6xl font-heading font-bold h-[120px] sm:h-[140px] lg:h-[160px] flex items-center justify-center md:justify-start">
              {PHRASES[0]}
            </h1>
            <p className="text-xl sm:text-2xl text-brand-primary font-medium">Hi, I'm Elanthamilan</p>
            <p className="text-lg text-muted-foreground max-w-lg mx-auto md:mx-0">
              A UX Designer with 9+ years of experience in Enterprise SaaS, EdTech, and HR Tech. I specialize in transforming complex requirements into user-friendly and intuitive digital solutions.
            </p>
            <div className="flex justify-center md:justify-start">
              <Button size="lg" asChild>
                <a href="/Elanable-uploads/Elanthamilan_Resume.pdf" download>
                  <Download className="mr-2 h-5 w-5" /> Download Resume
                </a>
              </Button>
            </div>

            {/* Brands */}
            <div className="space-y-4 pt-8">
              <h3 className="text-lg font-semibold text-muted-foreground">Brands I Have Worked With</h3>
              <div className="grid grid-cols-2 sm:grid-cols-4 gap-6 items-center justify-items-center">
                {BRANDS.map((brand) => (
                  <div key={brand.alt} className="flex flex-col items-center group w-full">
                    <img
                      src={brand.src}
                      alt={brand.alt}
                      className="h-10 sm:h-12 w-auto object-contain transition-transform duration-300 group-hover:scale-110 filter grayscale hover:grayscale-0"
                    />
                    <span className="text-xs text-muted-foreground mt-1 opacity-0 group-hover:opacity-100 transition-opacity duration-300">{brand.alt}</span>
                  </div>
                ))}
              </div>
            </div>

            {/* Stats */}
            <div className="grid grid-cols-2 sm:grid-cols-4 gap-6 pt-8">
              {STATS.map((stat) => (
                <div key={stat.label} className="text-center p-4 bg-card rounded-lg shadow-sm hover:shadow-md transition-shadow">
                  <p className="text-3xl sm:text-4xl font-bold text-brand-primary">{stat.value}</p>
                  <p className="text-sm text-muted-foreground">{stat.label}</p>
                </div>
              ))}
            </div>

            {/* Industries */}
            <div className="py-5 border-b border-slate-200 dark:border-slate-700">
              <h3 className="font-heading font-semibold text-foreground mb-3 text-md xs:text-lg">Industries I have Worked In</h3>
              <Accordion type="single" collapsible defaultValue="EdTech" className="w-full text-foreground">
                {INDUSTRIES.map((industry) => {
                  const IconComponent = ICON_MAP[industry.iconName];
                  return (
                    <AccordionItem key={industry.title} value={industry.title} className="border-b border-slate-200 dark:border-slate-700 last:border-b-0">
                      <AccordionTrigger className="flex items-center w-full py-3 px-1 text-left font-semibold text-base xs:text-lg rounded-md hover:bg-accent-bg focus-visible:bg-accent-bg focus-visible:ring-1 focus-visible:ring-brand-primary transition-colors data-[state=open]:bg-accent-light data-[state=open]:text-brand-primary">
                        {IconComponent && <IconComponent className="mr-2.5 h-4 w-4 xs:h-5 xs:w-5 flex-shrink-0 text-brand-primary" aria-hidden="true" />}
                        <span>{industry.title}</span>
                      </AccordionTrigger>
                      <AccordionContent className="pt-2 pb-3 px-1 text-foreground text-sm xs:text-base leading-relaxed bg-transparent rounded-b-md whitespace-pre-line">
                        {industry.content}
                      </AccordionContent>
                    </AccordionItem>
                  );
                })}
              </Accordion>
            </div>

            {/* Get In Touch */}
            <div className="space-y-4 pt-8">
              <h3 className="text-xl sm:text-2xl font-semibold">Get In Touch</h3>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                {CONTACT_ITEMS.map((item) => (
                  <Button
                    key={item.text}
                    variant="outline"
                    className="justify-start group"
                    onClick={item.type === 'email' || item.type === 'phone' ? () => copyToClipboard(item.text) : undefined}
                    asChild={!(item.type === 'email' || item.type === 'phone')}
                  >
                    {item.type === 'email' || item.type === 'phone' ? (
                      <>
                        <item.Icon className="mr-3 h-5 w-5 text-brand-primary transition-transform duration-300 group-hover:scale-110" />
                        {item.text}
                      </>
                    ) : (
                      <a href={item.href} target="_blank" rel="noopener noreferrer">
                        <item.Icon className="mr-3 h-5 w-5 text-brand-primary transition-transform duration-300 group-hover:scale-110" />
                        {item.text}
                      </a>
                    )}
                  </Button>
                ))}
              </div>
            </div>
          </div>

          {/* Right Column - Case Studies */}
          <div className="space-y-8 pt-0 md:pt-12"> {/* Adjust top padding for desktop */}
            <h2 className="text-2xl sm:text-3xl font-bold text-center md:text-left">Case Studies</h2>
            <div className="space-y-8">
              {caseStudiesData.map((study, idx) => (
                <motion.div
                  key={study.title}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: idx * 0.2 }}
                >
                  <CaseStudyCard
                    title={study.title}
                    year={study.year}
                    who={study.who}
                    what={study.what}
                    result={study.result}
                    image={study.image}
                    link={study.link}
                    tags={study.tags}
                    description={study.description}
                    index={study.index}
                    cardClassName={study.cardClassName}
                    accentColorName={study.accentColorName}
                    buttonVariant={study.buttonVariant}
                  />
                </motion.div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
});

Hero.displayName = "Hero";

export default Hero;
